/* Social hint dimensions */
.social {
  display: block; }

.social a {
  text-decoration: none; }

.social__item {
  display: inline-block;
  position: relative;
  margin: 56px 1rem 0;
  font-size: 18px;
  width: 45px;
  height: 45px;
  overflow: hidden;
  opacity: .33;
  will-change: transform; }

.social__icon {
  display: block;
  margin: 0 auto; }

.social__item--github:before, .social__item--facebook:before, .social__item--twitter:before {
  content: '';
  position: absolute;
  left: 50%;
  top: -56px;
  margin-left: -50px;
  box-sizing: border-box;
  width: 100px;
  height: 35px;
  background-color: #fff;
  text-align: center;
  color: rgba(0, 0, 0, 0.8);
  font-weight: 300;
  line-height: 35px; }

.social__item--github:after, .social__item--facebook:after, .social__item--twitter:after {
  content: '';
  position: absolute;
  left: 50%;
  top: -21px;
  width: 3px;
  height: 13px;
  background-color: #fff; }

.social__item--github:before {
  content: 'Github'; }

.social__item--facebook:before {
  content: 'Facebook'; }

.social__item--twitter:before {
  content: 'Twitter'; }

.social__item:before, .social__item:after {
  transform: translate(-38.4615384615px, 17.5px) rotate(-30deg);
  opacity: 0; }

.social__item:hover {
  overflow: visible;
  opacity: .8; }

.social__item:hover:before, .social__item:hover:after {
  transform: translate(0) rotate(0);
  opacity: 1;
  transition: all 0.3s; }
